* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
::-webkit-scrollbar {
    width: 0;
    height: 0;
    background-color: transparent;
}
html {
    display: flex;
    align-self: center;
    justify-content: center;
    font-family: "SCoreDream";
    font-weight: 400;
    font-size: 62.5%;
}
ul,
li {
    list-style: none;
}
a {
    text-decoration: none;
    cursor: pointer;
}

/* 화면 너비가 800px 이하이고 화면 높이가 500px 이하인 경우 */
@media (max-width: 800px) and (max-height: 500px) {
    html {
        font-size: 20%;
    }
}

/* 화면 너비가 800px 이상이고 화면 높이가 500px 이상인 경우 */
@media (min-width: 800px) and (min-height: 500px) {
    html {
        font-size: 25%;
    }
}

/* 화면 너비가 800px 이상이고 화면 높이가 500px 이하인 경우 */
@media (min-width: 800px) and (max-height: 500px) {
    html {
        font-size: 20%;
    }
}
